@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.3/css/all.min.css);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.nav {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 1rem;
  background-color: black;
  color: white;
}

.links {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.nav a {
  color: white;
  text-decoration: none;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1.5rem;
}

.nav a:hover {
  color: rgb(211, 69, 18);
}

/* .nav li:hover {
    background-color: blue;
}

.nav li.active {
    color: red;
} */

.container {
  /* margin-top: 1rem; */
  text-align: center;
}

@media (max-width: 768px) {
  .nav {
    flex-direction: column;
    align-items: flex-start;
  }

  .links {
    margin-top: 1rem;
  }

  .links li {
    margin: 0.5rem 0;
  }
}

#home {
    display: flex;
    flex-direction: column;
    margin-top: 3%;
    margin-right: 15%;
    justify-content: center;
    color: black;
}

#name {
    font-size: 200%;
    width: 105%;
    font-family: monospace;
    position: relative;
    -webkit-animation-name: example;
            animation-name: example;
    -webkit-animation-duration: 4s;
            animation-duration: 4s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    color: rgb(242, 70, 8);
}

@-webkit-keyframes example {
    0% {
        color: white;
        left: 0px;
        top: 0px;
    }

    25% {
        left: 100px;
        top: 0px;
    }

}

@keyframes example {
    0% {
        color: white;
        left: 0px;
        top: 0px;
    }

    25% {
        left: 100px;
        top: 0px;
    }

}

.main {
    display: flex;
    flex-direction: column;
    text-align: left;
} 

.headings {
    font-family: century;
    font-size: 2rem;
    text-align: left;
    margin-left: 5%;
}

#content_me {
    font-size: smaller;
    width: 85%;
    font-weight: 1;
    -webkit-animation-duration: 5s;
            animation-duration: 5s;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
} 

.resume {
    display: flex;
    border: 2px solid orangered;
    border-radius: 10px;
    color: orangered;
    background-color: white;
    font-size: 15px;
    justify-content: center;
    align-items: center;
    width: 110%;
    padding: 10px;
}

.resume:hover {
    cursor: pointer;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    background-color: orangered;
    color: whitesmoke;
    border: 2px solid orangered;
}

a {
    text-decoration: none;
}

.sujeeth_img {
    /* position: fixed; */
    margin: auto;
    display: flex;
    border-radius: 50%;
    height: 450px;
    border: 2px solid white;
    float: right;
}

.contents {
    margin: 0;
    margin-top: 4%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.contents div {
    display: flex;
    flex: 1 1;
    align-items: center;
}


/* For screens larger than 768px */
@media (min-width: 768px) {
  .contents {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 50px;
  }

  .right-content {
    flex: 1 1;
    display: flex;
    justify-content: flex-end;
  }

  .left-content {
    flex: 2 1;
    margin-right: 50px;
  }

  #name {
    font-size: 4rem;
  }

  #content_me {
    font-size: 1.5rem;
  }

  .resume-link {
    margin-top: 20px;
  }
}

/* For screens smaller than 768px */
@media (max-width: 768px) {
  .contents {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px;
  }

  .right-content {
    margin-bottom: 30px;
  }

  #name {
    font-size: 3rem;
  }

  #content_me {
    font-size: 1.2rem;
    text-align: center;
  }

  .resume-link {
    margin-top: 20px;
  }
}


/* Basic styling for the resume */

body {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
  line-height: 1.5;
  color: #333;
  margin: 0;
  padding: 0;
}

/* Styling for specific elements */

.Resume {
  max-width: 800px;
  margin: 0 auto;
  padding: 2rem;
  background-color: #fff;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  border-radius: 6px;

}

.header h1 {
  font-size: 3rem;
  margin: 0;
  color: orangered;
}

.header h2 {
  font-size: 1.5rem;
  margin: 0;
  color: #666;
}

.contact {
    margin-top: 3.5px;
}

.contact ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
}

.contact li {
  display: flex;
  /* align-items: center; */
  margin-right: 2rem;
  margin-bottom: 1rem;
}

.summary h3 {
  font-size: 2rem;
  margin: 2rem 0 1rem;
  color: #333;
}

.summary p {
  font-size: 1.2rem;
  margin: 0;
  color: #666;
}

.skills h3 {
  font-size: 2rem;
  margin: 2rem 0 1rem;
  color: #333;
}

.skills ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
}

.skills li {
  margin-right: 2rem;
  margin-bottom: 1rem;
  padding: 0.5rem;
  background-color: #f5f5f5;
  border-radius: 6px;
  color: #666;
  font-size: 1.2rem;
}

.experience h3 {
  font-size: 2rem;
  margin: 2rem 0 1rem;
  color: #333;
}

.job h4 {
  font-size: 1.5rem;
  margin: 0;
  color: #333;
}

.job h5 {
  font-size: 1.2rem;
  margin: 0;
  color: #666;
}

.job h6 {
  font-size: 1.2rem;
  margin: 0;
  color: #999;
}

.job ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.job li {
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
  color: #666;
}

.education h3 {
  font-size: 2rem;
  margin: 2rem 0 1rem;
  color: #333;
}

.achieve h3 {
    font-size: 2rem;
    margin: 2rem 0 1rem;
    color: #333;
  }

.achieve ul {
    list-style: none;
}

.degree h5 {
  font-size: 1.2rem;
  margin: 0;
  color: #666;
}

.degree h6 {
  font-size: 1.2rem;
  margin: 0;
  color: #999;
}

/* Styling for icons */

/* Adjust font size and color for all icons */

i {
  font-size: 1.2rem;
  color: #666;
}

/* Style specific icons */

.fas.fa-phone {
  margin-right: 0.5rem;
}

.fas.fa-envelope {
  margin-right: 0.5rem;
}

.fas.fa-globe {
  margin-right: 0.5rem;
}

ul {
    display: flex;
    justify-content: center;
    align-items: center;
}

li {
    padding: 2px;
}
.Slider {
  background-color: antiquewhite;
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
  justify-content: center;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
}

.project-image img {
  margin-top: 20px;
  width: 250px;
  margin-left: auto;
  margin-right: auto;
  border-radius: 10px;
}

.implement {
  display: flex;
}

.implement img{
  margin-top: 20px;
  width: 100px;
  height: 100px;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
}
.skill-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    grid-gap: 3.5%;
    gap: 3.5%;
    grid-column-gap: 3.5%;
    -webkit-column-gap: 3.5%;
            column-gap: 3.5%;
}

.skill h1 {
    font-size: xx-large;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: orangered;
}

.card-container {
    width: 200px;
    overflow: hidden;
    box-shadow: 0px 0px 15px -5px;
    border-radius: 5px;
    background-color: azure;
} 

.card-container:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    /* box-shadow: 0px 0px 15px 0px; */
}

.image-container img{
    margin-top: 5%;
    overflow: hidden;
    height: 150px;
}

.card-title {
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-weight: 1;
} 

.frontend, .backend {
    display: flex;
    margin-left: 5%;
    font-size: 150%;
    font-weight: 500;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

